<template>
  <div class="slider-verification">
    <div class="slider-wrapper">
      <img :src="computedBase64Data(bigImageBase64)" alt="Big Image" />
      <div
        class="slider"
        :style="{ transform: 'translateX(' + sliderX + 'px)' }"
        @mousedown="startDrag"
        @touchstart="startDrag"
        @mousemove="handleDrag"
        @touchmove="handleDrag"
        @mouseup="endDrag"
        @touchend="endDrag"
      ></div>
      <img :src="computedBase64Data(smallImageBase64)" alt="Small Image" />
    </div>
    <p v-if="isVerified" class="verification-text">验证成功！</p>
    <p v-else class="verification-text">请拖动滑块完成验证</p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sliderX: 0,
      isDragging: false,
      isVerified: false,
      bigImageBase64: 'your_big_image_base64_data', // 替换为后端给的大图的base64数据
      smallImageBase64: 'your_small_image_base64_data' // 替换为后端给的小图的base64数据
    };
  },
  computed: {
    computedBase64Data() {
      return (base64Data) => {
        return 'data:image/jpeg;base64,' + base64Data;
      };
    }
  },
  methods: {
    startDrag(event) {
      this.startX = event.clientX || event.touches[0].clientX;
      this.isDragging = true;
    },
    handleDrag(event) {
      if (!this.isDragging) return;
      const x = event.clientX || event.touches[0].clientX;
      this.sliderX = Math.min(Math.max(0, x - this.startX), 300); // 根据需求调整滑块可移动的范围
    },
    endDrag() {
      this.isDragging = false;
      this.isVerified = this.sliderX >= 200; // 根据需求调整完成验证的滑块位置
    }
  }
}
</script>
<style scoped>
.slider-verification {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slider-wrapper {
  position: relative;
  width: 300px; // 根据需求调整滑块宽度
  height: 200px; // 根据需求调整滑块高度
  overflow: hidden;
  margin-bottom: 20px;
}
.slider-wrapper img {
  width: 100%;
  height: auto;
}
.slider {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 80px; // 根据需求调整滑块宽度
  background-color: #1890ff; // 根据需求调整滑块的样式
  border-radius: 5px; // 根据需求调整滑块的样式
  cursor: grab;
  transition: all 0.2s;
}
.verification-text {
  color: #1890ff; // 根据需求调整验证文字的颜色
}
</style>



